
let pagina = 1;

document.addEventListener('DOMContentLoaded', function (){
    scrollNav();
    boton();
    tabs();
    mostrarSeccion();
});

function navObserver(){
    const enlaces = document.querySelector('.main-menu a');


    const observer = new IntersectionObserver(function (entries){

        enlaces.forEach(enlace =>{
            if(entries[0].isIntersecting){
                enlace.classList.remove('main-menu-active');
            }else {
                enlace.classList.add('main-menu-active')
            }
        });
        
    });

    // elemento a observar
    observer.observe(document.querySelector('.proyects'));
}

function scrollNav() {
    const enlaces = document.querySelectorAll('.main-menu a');
    const btn = document.querySelector('.main-menu');

    enlaces.forEach( enlace => {
        enlace.addEventListener('click', function(e){
            e.preventDefault()

            const seccion = document.querySelector(e.target.attributes.href.value);
            btn.classList.remove('nav-activado');
            seccion.scrollIntoView({
                behavior: 'smooth',
            });
        });
    });
}

function boton(){

    const btn = document.querySelector('.nav-toggle');
    const menu = document.querySelector('.profile');

    btn.addEventListener('click', function (){
        menu.classList.toggle('nav-activado');
    });
}

function mostrarSeccion(){
    const seccionActual =  document.querySelector(`#paso-${pagina}`);
    seccionActual.classList.add('show-seccion');

    const tab = document.querySelector(`[data-paso="${pagina}"]`);
    tab.classList.add('style');
}



function tabs(){
    const enlaces = document.querySelectorAll('.buttons-container button');

    enlaces.forEach( enlace => {
        enlace.addEventListener('click', e => {
            e.preventDefault();

            pagina = parseInt(e.target.dataset.paso);

            // elimina mostrar-seccion donde dimos click
            document.querySelector('.show-seccion').classList.remove('show-seccion')
            
            // agrega mostrar-seccion donde dimos click
            const seccion = document.querySelector(`#paso-${pagina}`);
            seccion.classList.add('show-seccion');

            // elimina la clase actual en el tab anterior
            document.querySelector('.buttons-container .style').classList.remove('style');

            // agrega la clase de actual en le 
            const tab = document.querySelector(`[data-paso="${pagina}"]`);
            tab.classList.add('style');

        })
    })
}